/* Nav Bar */

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #333;
  color: #fff;
}

.nav-title {
  font-size: 1.5rem;
  margin: 14px 16px;
}

.navbar-links ul {
  margin: 0;
  padding: 0;
  display: flex;
}

.navbar-links li {
  list-style: none;
}

.navbar-links li a {
  text-decoration: none;
  color: #fff;
  padding: 1rem;
  display: block;
}

.navbar-links li:hover {
  background-color: #555;
}

.toggle-button {
  position: absolute;
  top: 1.25rem;
  right: 1rem;
  display: none;
  flex-direction: column;
  justify-content: space-between;
  text-decoration: none;
  color: #fff;
}

/* Landing Page */
.landing-page {
  height: 100vh;
  padding: 0 16px;
}

.main-heading {
  margin: 0;
  font-size: clamp(38px, 8vw, 78px);
  font-weight: 600;
}

.sub-heading {
  font-size: clamp(18px, 2.5vw, 38px);
}

/* About Me */

.about-me {
  text-align: center;
  padding: 0 16px;
}

.about-me img {
  height: 400px;
  width: 400px;
  border-radius: 10%;
}

/* Work */
.work {
  text-align: center;
}

.work img {
  padding: 14px 20px;
}

.card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  height: 83%;
  width: 33%;
  margin: 0.5% 1%;
  transition: 0.3s;
  animation: ease-in-out;
  display: inline-block;
  text-align: center;
  padding: 0 0 1% 0;
}

.card img {
  text-align: center;
  max-width: 90%;
  height: 200px;
}

.card:hover {
  transform: scale(1.1);
  box-shadow: 0px 0px 15px 0px;
}

.card a {
  color: #000;
  text-decoration: none;
}

.work-ul {
  text-align: left;
  list-style: none;
}

.user-tasks-btn:hover {
  opacity: 0.7;
}

/* Contact */
.contact {
  text-align: center;
}

.mail-link {
  color: #000;
}

.contact a {
  color: #000;
  text-decoration: none;
  margin: 14px 16px;
}

/* Media queries */

/* Landing Page */
@media screen and (max-width: 600px) {
  .toggle-button {
    display: flex;
  }

  .navbar-links {
    display: none;
    width: 100%;
  }

  .navbar-links ul {
    width: 100%;
    flex-direction: column;
  }

  .navbar-links.active {
    display: flex;
  }

  .navbar {
    width: 100%;
    flex-direction: column;
    align-items: flex-start;
  }
}